
























import { Component, Vue } from "vue-property-decorator";
import api from "@/data/apis";
import { Comment } from "@/types/comment";

@Component({
  components: {},
})
export default class CommentsRecived extends Vue {
  comments: Comment[] = [];
  words: Comment[] = [];
  waiting = true;

  mounted(): void {
    this.refresh();
  }

  async refresh(): Promise<void> {
    this.waiting = true;
    var response = await api.getComments();
    this.comments = response.body.filter(
      (x) => x.word !== null && x.word.length > 15
    );
    this.words = response.body.filter(
      (x) => x.word !== null && x.word.length < 16
    );
    this.waiting = false;
  }
}
